import React, {Component} from 'react'
import {Footer} from 'react-materialize'
import '../styles/Footer.css'

class Footers extends Component {
  twitterLink (e) {
    window.location.assign('http://twitter.com')
  }
  instagramLink (e) {
    window.location.assign('http://instagram.com')
  }
  facebookLink (e) {
    window.location.assign('http://facebook.com')
  }
  linkedInLink (e) {
    window.location.assign('http://linkedin.com')
  }

  render () {
    return (
      <div className='page-footer'><Footer copyrights='&copy; 2018 Copyright Dollar Beer Club'
        links={
          <ul className='right' style={{margin: 30}}>
            <li><a onClick={(e) => { this.instagramLink(e) }}><img className='grey-text text-lighten-3 blue-text hoverable center' style={{height: 30, marginBottom: 10}}
              src='data:image/png;base64,' /></a></li>
            <li><a onClick={(e) => { this.facebookLink(e) }}><img className='grey-text text-lighten-3 blue-text hoverable' style={{height: 30, marginBottom: 10}}
              src='data:image/png;base64,' /></a></li>
            <li><a onClick={(e) => { this.twitterLink(e) }}><img className='grey-text text-lighten-3 blue-text hoverable' style={{height: 30, marginBottom: 10}}
              src='data:image/png;base64,' /></a></li>
            <li><a onClick={(e) => { this.linkedInLink(e) }}><img className='grey-text text-lighten-3 blue-text hoverable' style={{height: 30, marginBottom: 10}}
              src='data:image/png;base64,' /></a></li>
          </ul>
}
        >
        <h4 className='white-text change' style={{fontFamily: 'Yanone Kaffeesatz', fontWeight: 'lighter'}}>Dollar Beer Club</h4>
        <p className='grey-text text-lighten-4' style={{fontFamily: 'Yanone Kaffeesatz', fontWeight: 'lighter'}}>Lorem ipsum dolor sit amet, consetetur sadipscing elitr, sed diam nonumy eirmod tempor invidunt ut labore et dolore magna aliquyam erat, sed diam voluptua. At vero eos et accusam et justo duo dolores et ea rebum. Stet clita kasd gubergren, no sea takimata sanctus est Lorem ipsum dolor sit amet. Lorem ipsum dolor sit amet, consetetur sadipscing elitr, sed diam nonumy eirmod tempor invidunt ut labore et dolore magna aliquyam erat, sed diam voluptua. At vero eos et accusam et justo duo dolores et ea rebum. Stet clita kasd gubergren, no sea takimata sanctus est Lorem ipsum dolor sit amet.</p>
      </Footer>
      </div>
    )
  }
}

export default Footers



